<template>
	<!-- 难点2，任何pages文件中的.vue文件都能随意将compons中的.vue文件当子组件 -->
	<!-- 该组件是首页展示的新闻列表，绑定响应函数，点击后跳转到新闻详情页 -->
	<view class="newbox" @click="goDetail()">
		<!-- 首页新闻展示页 -->
		<view class="pic">
		<image :src="item.picurl" mode="aspectFill"></image>
		</view>
		
		
		<view class="text">
			<view class="title">{{item.title}}</view>
			<!-- 注意像v-if这种指令语法前面不需要加v-module即： -->
			<!--item.looktime的值是字符串，除了空串转布尔为false，其余都是true -->
			<view class="info" v-if="!item.looktime">
				<text>{{item.author}}</text>
				<text>{{item.hits}}</text>
			</view>
			<!--v-else不需要有值，只有当所有v-if与v-else-if的值都为false时（即v-if与v-else-if都生效即所在标签都隐藏），才会渲染v-else所在标签，否则v-else会隐藏所在标签代码。就像if、else-if、else一样 -->
			<view class="title" v-else>{{item.looktime}}</view>
		</view>
		
	</view>
</template>

<script>
	export default {
		name:"newsbox",
		data() {
			return {
				
			};
		},
		methods:{
			
			goDetail(){
				// console.log(this.item)
				uni.navigateTo({
					
					// 难点。es6新添加的运算符反双引号``，反双引号中除了${}里面的内容当作表达式看，其余内容都当字符串看.下一行代码意思为带着父组件index.js中请求到的新闻列表数据newsarr中的每个元素items中的属性classname、id跳转到新闻详情页即detail.vue页面。
					// prop配置项接收到的数据会放到组件表面，所以需要在item前面加上this
					url:`/pages/detail/detail?cid=${this.item.classid}&id=${this.item.id}`
				})
			}
		},
		props:{
			// props配置项的对象写法，用于对接收到的值做类型限制，item未接收到值时用默认值，接收到值用接收到的值
			item:{
				type:Object,
				default(){
					return{
						title:"新闻默认标题",
						author:"LiYanGuoSir",
						hits:758,
						picurl:"../../static/images/01.jpg",
						// 难点1：由于点击首页和个人中心后都会给item传值，那么展示这两个item中的属性的值时，就得展示时间就不能有新闻标题和作者，反之一样。需求知道了，怎么实现呢？
						// 思路：用looktime的值作为v-if的值，意思为如果接收到了looktime属性的值，那么looktime的值转换成布尔值就是true，就隐藏展示作者和浏览量hits所在div，反之依然。
						looktime:"2023-6-12 20:53:15"
					}
				}
			}
		}
	}
</script>

<style lang="scss">
.newbox{
		 display: flex;
		 justify-content: space-between;
	
 .pic{
	 width: 230rpx;
	 height: 160rpx;
     // flex-grow: 1;
	 image{
		 width: 100%;
		 height: 100%;
	    }
	 }
	.text{
		// 图片右边的区域大小设置成去掉图片大小后的百分比宽度，否则过长的标题会挤压图片的大小。不用担心标题文字部分区域设置的百分比宽度过小，给它加个父元素有空白空间时等比例扩大即可。标题长度太长一直挤压图片，原来没有给标题title限制宽度，不然它会一直挤压其他兄弟元素的大小
		width:20%;
		padding-left: 20rpx;
		// border: 1px solid red;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		// 开启了flex布局的父元素的子元素中能添加的属性flex-grow,当父元素里有多余空白空间时，子元素会根据所有子元素中该属性的值来按比例分配空余空白空间（.pic中该属性的值是1，它是3，则分配的空白区域就是四分之三）。可简写成flex:1;
		flex-grow: 1;
		.title{
			width:100%;
			// height: 100rpx;
			font-size: 36rpx;
			color:#333;
			overflow: hidden;
			// 设置text标签中的文字最多显示两行，多出来的部分hidden隐藏，并用省略号ellipsis代替
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			// line-clamp: 2;
			-webkit-box-orient:vertical;
			
		}
		.info{
			font-size: 26rpx;
			color:#999;
			text{
				padding-right:30rpx;
			}
		}
	}
 
 }
</style>